.SecondaryHeader_Body{
    height: 62px;
    width: 100%;
    background-color: rgba(0, 87, 194, 10%);
    display: flex;
    justify-content: space-between;
}

.SecondaryHeader_Body_Jobs{
    display: flex;
}

.SecondaryHeader_Jobs{
    font-size: 14px;
    margin: 21px 5px;
    color: #0057C2;
}

.SecondaryHeader_All_Categories{
    height: 25px;
    padding: 21px 0;
    margin-right: 20px;
}

.SecondaryHeader_All_Categories Button{
    width: 150px;
    border: 1px solid #0057C2;
    border-radius: 10px;
    padding: 5px 15px 5px 15px;
    font-size: 12px;
    color: #0057C2;
    background-color: white;
}
